import dnIcon from '../../components/dn-icon/dn-icon.vue'
export default {
    components: {
		dnIcon
		},
		data() {
			return {
				//点赞创建时间
				likeStatus: [],
				//点赞的数量
				likes: [],
				//是否已经点赞 true or false
				color: [],
				date: new Date(),
				userDto:{},
				id:1,
				journalId:1,
				listData:[],
				banner:{},
				// bannercom: {}
			};
		},
		methods: {
			//点赞换色
			changeColor(index) {
				//已经点赞
				if (this.color[index]) {
					//改变数组状态为false
					this.color.splice(index, 1, false);
					//点赞数减一
					this.likes.splice(index, 1, this.likes[index] - 1);
				} else {
					//改变数组状态为true
					this.color.splice(index, 1, true);
					//点赞数减一
					this.likes.splice(index, 1, this.likes[index] + 1);
				}
			},
			// 点赞的方法
			thumbUp(journalId) {
				// alert(journalId)
				// this.likeStatus[index] = this.formatDateTime();
				// this.changeColor(index);
				// console.log(this.likeStatus[index]);
				this.axios({
					method: 'put',
					url: this.GLOBAL.baseUrl + '/journal/user/journal/addlike',
					data: JSON.stringify({
						journalId: journalId,
						userId: JSON.parse(localStorage.getItem('user')).id
					}),
					headers: {
						'Content-Type': 'application/json'
					}
				}).then(res => {});
			},
			// 取消点赞的方法
			thumDown(journalId) {
				// alert(journalId)
				// this.likeStatus[index] = null;
				// this.changeColor(index);
				// console.log(this.likeStatus[index]);
				this.axios({
					method: 'delete',
					url: this.GLOBAL.baseUrl + '/journal/user/journal/cancellike',
					data: JSON.stringify({
						journalId: journalId,
						userId: JSON.parse(localStorage.getItem('user')).id
					}),
					headers: {
						'Content-Type': 'application/json'
					}
				}).then(res => {});
			},
			tapPopup(e){
				uni.showToast({
					title:e.title,
					icon:'none'
				})
			},
			taptext(e,index){
				this.x = e.touches[0].clientX
				this.y = e.touches[0].clientY
				
				this[`value${index}`] = !this[`value${index}`]
			},
			
			getBanner() {
				let data= {
					id:this.userDto.id,
				};
			    uni.request({
			        url: this.$baseUrl+'/journal/user/journaldetail/'+this.userDto.id,
					method:'POST',
			        success: (data) => {
			            uni.stopPullDownRefresh();
			            if (data.statusCode == 200) {
			                this.banner = data.data;
			            }
			        },
			        fail: (data, code) => {
			            console.log('fail' + JSON.stringify(data));
			        }
			    })
			},
			getList(){
				var _self = this;
				uni.request({
					url: this.$baseUrl + '/journal/index/data',
					method:'POST',
					data:{
						id:this.userDto.id,
					},
					success: (res) => {
						//请求成功后将日志列表数据赋值给listData
						console.log(res.data.data)
						this.listData = res.data.data
						
					}
				});
			},
			goDetail(item){
				let detail = {
					id:item.id,
					userId:item.userId,
					thumbnail:item.thumbnail,
					createTime:item.createTime,
					comments:item.comments,
					title:item.title,
					avatar:item.avatar,
					nickname:item.nickname,
					likes:item.likes,
					content:item.content
				}
			    uni.navigateTo({
					url: "../j_detail/j_detail?detailDate=" + encodeURIComponent(JSON.stringify(detail))
			    })
			},
		},
		onLoad() {
			let that = this;
			uni.getStorage({
				key: 'user',
				success: function(res) {
					that.userDto = res.data;
					console.log(that.userDto);
					that.getList();
					that.getBanner();
				}
			});
		}
	
}	
